import React from "react"
import MePhoto from "../images/me.png"
import { Row, Col, Container } from "reactstrap"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import AniLink from "gatsby-plugin-transition-link/AniLink"
import { faGraduationCap, faHome } from "@fortawesome/pro-duotone-svg-icons"

const headerStyle = page => {
  if (page === "Home") return "pt-3 bg-gradient-primary"

  return "pt-3 bg-primary"
}

const menu = page => {
  if (page === "Home")
    return (
      <AniLink
        id="menuButton"
        swipe
        direction="left"
        duration={0.35}
        entryOffset={80}
        to="/curriculum"
        className="link"
      >
        <FontAwesomeIcon
          className="text-white"
          icon={faGraduationCap}
          size="2x"
        />
      </AniLink>
    )
  else
    return (
      <AniLink
        id="menuButton"
        swipe
        direction="right"
        duration={0.35}
        entryOffset={80}
        to="/"
        className="link"
      >
        <FontAwesomeIcon
          className="text-white"
          icon={faHome}
          size="2x"
        />
      </AniLink>
    )
}

export default function Header(props) {
  console.log(props)
  return (
    <div>
      <header className={headerStyle(props.page)}>
        <Container
          className="pb-3"
          style={{ borderBottom: "2px solid rgba(255,255,255,0.5)" }}
        >
          <Row className="align-items-center">
            <Col xs={3} md={2}>
              <AniLink fade to="/">
                <img
                  src={MePhoto}
                  className="img-fluid rounded-circle float-right"
                  style={{ maxWidth: "64px" }}
                  alt="Profile photo"
                />
              </AniLink>
            </Col>
            <Col xs={7} md={8}>
              <AniLink className="text-white" fade to="/">
                <strong>Alessandro D'Orazio</strong>
              </AniLink>
              <br />
              <span className="font-weight-light text-white-50">
                Sviluppatore Software
              </span>
            </Col>
            <Col xs={2}>{menu(props.page)}</Col>
          </Row>
        </Container>
      </header>
    </div>
  )
}
